@import url(https://fonts.googleapis.com/css?family=Oswald:300,400&display=swap);

html {
  margin: 0;
  padding: 0;
  overflow: hidden;
  height: 100vh;
  box-sizing: border-box;
}
body {
  font-size: 10vh;
  font-family: Oswald;
  background-color: #fff;
}

.mainScreen button.back {
  height: 7vh;
  margin: 3vh 0;
  font-size: 3vh;
  width: 20vw;
  position: fixed;
  top: 85vh;
  left: 5vw;
  line-height: 7vh;
  padding: 0;
}

img.globe {
  height: 600px;
  border: none;
  position: fixed;
  top: 50vh;
  left: 75vw;
  z-index: -1;
  -webkit-animation: spin 60s linear infinite, appear 5s;
          animation: spin 60s linear infinite, appear 5s;
  -webkit-transform: rotate(0deg);
          transform: rotate(0deg);
}

.mainScreen {
  font-size: 5vh;
  height: 100%;
  overflow: hidden;
}

footer {
  position: fixed;
  top: 97vh;
  left: 0;
  width: 100vw;
  font-size: 2vh;
  color: white;
  background-color: black;
}

img {
  height: 200px;
  border: 4px solid black;
  -webkit-animation: appear 0.3s;
          animation: appear 0.3s;
}

button img {
  height: 70px;
  margin: 0 auto;
  border: none;
}

button {
  margin: 5px;
  height: 20vh;
  width: 35vw;
  font-size: 30px;
  font-family: Oswald;
  padding: 2vw;
  cursor: pointer;
  background-color: #777;
  border: 2px solid black;
  /* line-height: 100px; */
  -webkit-transition: 0.3s;
  transition: 0.3s;
  border-radius: 5%;
  -webkit-animation: appear 0.3s;
          animation: appear 0.3s;
}

button:hover {
  background-color: #999;
}

.leftPanel {
  width: 50vw;
  padding: 2vw;
  text-align: center;
  top: 5vh;
  position: absolute;
}

.leftPanel h1 {
  font-size: 3vh;
  margin: 1vh;
}

.leftPanel div {
  font-size: 4vh;
}

.rightPanel {
  position: absolute;
  top: 5vh;
  left: 60vw;
}

.gameOver {
  text-align: center;
  font-size: 50px;
  margin: 0 auto;
  padding: 10vw;
}

.gameOver button {
  margin: 5vw;
}

@media (orientation: portrait) {
  body {
    margin: 0;
    padding: 0;
    font-size: 5vh;
    font-family: Oswald;
    overflow: hidden;
  }

  img.globe {
    position: fixed;
    top: 60vh;
    left: 65vw;
  }

  .mainScreen {
    font-size: 3vh;
    text-align: center;
  }

  img {
    height: 15vh;
    border: 4px solid black;
    -webkit-animation: appear 0.3s;
            animation: appear 0.3s;
  }

  button img {
    height: 10vh;
    margin: 0 auto;
    border: none;
  }

  button {
    margin: 5px;
    padding: 2vh;
    height: 15vh;
    width: 45vw;
    font-size: 5vw;
  }

  .leftPanel {
    width: 100vw;
    height: 40vh;
    padding: 2vw;
    text-align: center;
    top: 0;
  }

  .leftPanel h1 {
    font-size: 3vh;
  }

  .rightPanel {
    position: absolute;
    top: 35vh;
    left: 0;
    height: 60vh;
    width: 100vw;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-flow: column wrap;
    align-content: center;
  }

  .rightPanel div {
    display: inline-block;
  }

  .rightPanel button {
    height: 20vh;
    width: 45vw;
    font-family: Oswald;
  }

  .answer {
    display: inline-block;
  }

  .gameOver {
    text-align: center;
    font-size: 50px;
    margin: 0 auto;
    padding: 10vw;
  }

  .gameOver button {
    margin: 5vw;
  }
}

@media (max-width: 900px) and (orientation: landscape) {
  .mainScreen {
    font-size: 5vh;
    height: 100%;
    overflow: hidden;
  }

  img {
    height: 15vw;
    border: 4px solid black;
    -webkit-animation: appear 0.3s;
            animation: appear 0.3s;
  }

  button img {
    height: 5vw;
    margin: 0 auto;
    border: none;
  }

  button {
    margin: 1vw;
    height: 18vh;
    width: 25vw;
    font-size: 4vh;
    text-align: center;
  }

  .leftPanel {
    width: 50vw;
    padding: 2vw;
    text-align: center;
  }

  .rightPanel {
    position: absolute;
    top: 5vh;
    left: 60vw;
  }

  .gameOver {
    text-align: center;
    font-size: 50px;
    margin: 0 auto;
    padding: 10vw;
  }

  .gameOver button {
    margin: 5vw;
  }
}

@-webkit-keyframes appear {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes appear {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@-webkit-keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

h1 {
  text-align: center;
}

.selectScreen button {
  font-size: 3vw;
  line-height: 5vh;
  text-align: center;
  width: 48vw;
  height: 20vh;
}

@media (orientation: portrait) {
  .selectScreen button {
    font-size: 0;
    width: 40vw;
  }
}

@media (max-width: 900px) and (orientation: landscape) {
  h1 {
    text-align: center;
    margin: 3vh;
  }
  .selectScreen button {
    font-size: 3vw;
    line-height: 5vh;
    text-align: center;
    width: 46vw;
    height: 20vh;
  }
}

.buttons {
  display: flex;
}

.buttons button {
  width: 40vw;
  margin: 5vw;
  text-align: center;
}

.introScreen {
  height: 100vh;
  width: 100vw;
  text-align: center;
}

h1.title {
  display: block;
  position: absolute;
  top: 8vh;
  left: 15vw;
  -webkit-animation: fadein 2s;
          animation: fadein 2s;
  font-size: 10vh;
  margin-bottom: 0;
}

.Nor {
  color: blue;
  margin-top: 0;
  position: absolute;
  top: 35vh;
  left: 15vw;
  -webkit-animation: rollin 2s;
          animation: rollin 2s;
}

@media (orientation: portrait) {
  h1.title {
    display: inline-block;
    top: 10vh;
    text-align: left;
    left: 15vw;
    -webkit-animation: appear 2s;
            animation: appear 2s;
    font-size: 5vh;
    margin-bottom: 0;
  }

  .Nor {
    top: 30vh;

    -webkit-animation: appear 2s;

            animation: appear 2s;
  }
}

@-webkit-keyframes fadein {
  0% {
    font-size: 0;
  }
  20% {
    font-size: 0;
  }
  100% {
    font-size: 10vh;
  }
}

@keyframes fadein {
  0% {
    font-size: 0;
  }
  20% {
    font-size: 0;
  }
  100% {
    font-size: 10vh;
  }
}

@-webkit-keyframes rollin {
  0% {
    left: -70vw;
  }
  50% {
    left: -70vw;
  }
  100% {
    left: 15vw;
  }
}

@keyframes rollin {
  0% {
    left: -70vw;
  }
  50% {
    left: -70vw;
  }
  100% {
    left: 15vw;
  }
}

.gameOver {
  font-size: 6vh;
  text-align: left;
}

.gameOver img {
  height: 5vh;
  border: 2px solid black;
}

.results {
  height: 40vh;
  position: fixed;
  background-color: white;
  top: 0;
  left: 0;
  width: 100vw;
  padding-top: 10vh;
  text-align: center;
}

.correctAnswers {
  font-size: 3vh;
  padding: 2vh;
  position: absolute;
  height: 50vh;
  animation: appear 3s, rollcall 20s 1s linear both, appear 3s 20s reverse;
  z-index: -1;
}

@-webkit-keyframes rollcall {
  0% {
    -webkit-transform: translateY(100%);
            transform: translateY(100%);
  }
  100% {
    -webkit-transform: translateY(-300%);
            transform: translateY(-300%);
  }
}

@keyframes rollcall {
  0% {
    -webkit-transform: translateY(100%);
            transform: translateY(100%);
  }
  100% {
    -webkit-transform: translateY(-300%);
            transform: translateY(-300%);
  }
}

@-webkit-keyframes appear {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes appear {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

